<template>
    <div class="box">
        <form v-on:submit.prevent="submitForm">
            <div class="mdui-textfield mdui-textfield-floating-label">
                <i class="mdui-icon material-icons">lock</i>
                <label class="mdui-textfield-label">密码</label>
                <input class="mdui-textfield-input" type="password" id="password" name="password" v-model="form.password"/>
            </div>
            <div style="text-align: center; padding-top: 20px;">
                <button class="mdui-btn mdui-btn-raised mdui-ripple mdui-color-blue-50" style="width: 200px;" id="LoginBtn">登录</button>
            </div>
        </form>
    </div>
</template>

<script>
import axios from "axios";
import mdui from "mdui";

export default {
    name: "navLogin",
    data(){
      return {
          form: {
              password: ''
          }
      }
    },
    methods: {
        submitForm() {
            axios.post('api/login', this.form)
                .then((res) => {
                    //Perform Success Action
                    if (res.data.code === 2001) {
                        mdui.snackbar({
                            message: '服务繁忙',
                            position: 'right-top',
                        });
                    } else if (res.data.code === 2006) {
                        mdui.snackbar({
                            message: '密码错误',
                            position: 'right-top',
                        });
                    }else if (res.data.code === 1000) {
                        // 密码正确
                        localStorage.setItem("Bearer", res.data.data.Token)
                        this.$store.dispatch("tChangeData")
                        // this.$router.push({path: '/'})
                        window.open("/", "_self")
                    }
                }).catch((error) => {
                    // error.response.status Check status code
                    mdui.snackbar({
                        message: error,
                        position: 'right-top',
                    });
                });
        }
    }
}
</script>

