<template>
    <div>
<!--        Header-->
        <nav-l-header />

<!--        Login-->
        <nav-login />

<!--        Footer-->
        <nav-l-footer />
    </div>
</template>

<script>
import navLogin from "@/components/Login/navLogin";
import navLHeader from "@/components/Login/navLHeader";
import navLFooter from "@/components/Login/navLFooter";

export default {
    name: "LoginView",
    components: {
        navLHeader,
        navLogin,
        navLFooter
    },
    methods: {
        // 设置背景图
        BG: function () {
            if (this.wData['Background'] === ""){
                this.styleCss = `background: -webkit-gradient(linear, 0% 0%, 100% 0%, from(` + this.wData['BackgroundColorA'] + `), to(` + this.wData['BackgroundColorB'] + `));`
            } else {
                this.styleCss = `background: url("` + this.wData['Background'] + `") repeat scroll left top;`
            }
            document.body.style = this.styleCss
        }
    },
    computed: {
        // 配置数据
        wData(){
            return this.$store.state.LoginConfig
        }
    },
    mounted() {
        this.BG()
    }
}
</script>

