<template>
    <div id="footer">
        网站信息
        <div class="inline" v-for="d in wData['text']" :key="d">
            <div v-if="d['TextLink']">
                &ensp;&ensp;|&ensp;&ensp;
                <a :href="d['TextLink']" :style="{color: wData['FColor']}">
                    {{ d["Text"] }}
                </a>
            </div>
            <div v-else>
                &ensp;&ensp;|&ensp;&ensp;
                <a :style="{color: wData['FColor']}">
                    {{ d["Text"] }}
                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "navLFooter",
    computed: {
        // 配置数据
        wData(){
            return this.$store.state.LoginConfig
        }
    }
}
</script>

<style scoped>
#footer {
    text-align: center;
    margin-top: 420px;
    font-family: MyriadSetPro-Thin;
}
#footer div {
    display: inline;
}
.inline {
    display: inline;
}
</style>
